








































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class HelloWorld extends Vue {
  @Prop() private msg!: string;
  private items: [] = [];

  private created() {
    fetch('/api/values')
      .then((res) => res.json())
      .then((data) => {
        this.items = data;
      });
  }
}
